.container {
  justify-content: center;
}

.formContainer {
  padding: 1rem 0;
}

.form {
  padding: 1rem;
  background-color: white;
  -webkit-box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.3);
  -moz-box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.3);
  box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.3);

  .button {
    background-color: #1ac0ea;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0.25rem 0.25rem 0.25rem 0;
    width: 12rem;
  }

  .input {
    border: 1px solid hsl(0,0%,80%);
    height: 2.5rem;
    padding: 0 18px;
  }

  .input.email {
    margin-right: 0;
    border-right: none;
  }

  .area {
    border: 1px solid hsl(0,0%,80%);
    resize: none;
    height: 10rem;
    padding: 8px 18px;

  }

  .area, .input {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 0.9rem;
    color: #333333;
    width: 100%;
    margin: 0.25rem;
    outline: none;
  }

  .input::placeholder, .area::placeholder {
    color: #333333;
  }
}

.info {
  //height: 70%;
  //max-height: 300px;
  //align-self: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;

  .unit {
    display: flex;
    align-items: center;
    //flex-grow: 0;
    width: 80%;
    padding: 1rem 0;

    .icon {
      flex-grow: 0;
      justify-content: center;
      min-width: 3rem;
    }

    .icon.address {
      padding-top: 0.25rem;
      //align-self: flex-start;
    }

    .text {
      flex-grow: 1;
      font-family: 'Montserrat', sans-serif;
      font-weight: 500;
      color: #333333;

    }

  }

  .unit.address {
    //align-items: flex-end;
    align-items: flex-start;
  }

  .unit.email {
    //align-items: flex-start;
  }
}

.modal {
  font-family: 'Montserrat', sans-serif;
  padding: 1rem 0.5rem;
  font-size: 1.2rem;
  text-align: center;
}