.infoContainer {
  justify-content: center;
}

.text {
    text-align: justify;
    padding: 1rem;
    flex-grow: 1;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
}

.imageContainer {
  flex-grow: 0;
  padding: 1rem;
  display: flex;
  justify-content: center;

  .image {
    overflow: hidden;
    object-fit: cover;

  }
}

.button {
  margin: 2rem 0;
}

.additional {
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  //justify-content: center;
  //align-items: center;
}

.title {
  padding: 0.5rem 0;
  font-size: 2rem;
  text-align: center;
  justify-content: center;
  font-family: 'Akrobat Bold', sans-serif;
}

.simpleText {
  padding: 0.5rem 0;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  text-align: justify;
}

.simpleText.bold {
  padding-top: 1.5rem;
  font-size: 1.15rem;
  font-weight: 500;
  text-align: center;
  justify-content: center;
}

.countryTitle {
  padding: 0.5rem 0;
  display: flex;
  justify-content: center;
  align-items: center;

  .name {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 1.2rem;
  }

  .icon {
    margin-left: 1rem;
    -webkit-box-shadow: 0px 0px 5px -1px rgba(0,0,0,0.4);
    -moz-box-shadow: 0px 0px 5px -1px rgba(0,0,0,0.4);
    box-shadow: 0px 0px 5px -1px rgba(0,0,0,0.4);
  }
}

.countryText {
  padding: 0.5rem 0;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  text-align: justify;
}

.bull {
  font-weight: 700;
  padding: 0 0.5rem;
}