.container {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  color: white;
}

.row {
  display: flex;
  justify-content: center;
  align-items: center;
}

.left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .copyright {
      margin-top: 0.25rem;
    }

    .agreement {
      color: #1ac0ea;
      text-decoration: underline;
    }
  }

  margin-bottom: 1.5rem;
}

.right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  //height: 100%;

  .connect {
    display: flex;
    align-items: center;

    .telegram {
      margin: 0 1.5rem;
    }
  }

  .subscribe {
    display: flex;

    .input {
      height: 2.5rem;
      font-family: 'Montserrat', cursive;
      font-style: italic;
      font-weight: 300;
      padding: 0 1rem;
    }

    .button {
      height: 2.5rem;
      font-weight: bold;
      background-color: #1ac0ea;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 10rem;
    }
  }

  margin-bottom: 1.5rem;

}

.subfooter {
  text-align: justify;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.8rem;
}