.container {
  justify-content: space-between;
  //justify-content: center;
}

.serviceContainer {
  padding: 0 5rem;
}

.service {
  display: flex;
  flex-direction: column;
  padding: 1rem 0;

  .header {
    display: flex;

    .icon {
      flex: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      min-width: 4rem;
    }

    .title {
      font-family: 'Akrobat Bold', sans-serif;
      font-size: 1.5rem;
    }
  }

  .info {
    padding-left: 4rem;
    flex: 1;
    display: flex;
    flex-direction: column;
    color: #333333;

    .text {
      font-family: 'Montserrat', sans-serif;
      font-weight: 400;
      padding: 0.5rem 0;
    }

    .button {
      font-family: 'Montserrat', sans-serif;
      font-weight: 400;
      font-size: 0.85rem;
      margin-top: 0.5rem;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #1ac0ea;
      height: 2rem;
      border-radius: 1rem;
      width: 10rem;
    }
  }
}

.modal {
  border-radius: 1rem !important;
  overflow: hidden;
}

.content {
  background-color: white;
  padding: 1rem 2rem;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

.productTitle {
  font-family: 'Akrobat Bold', sans-serif;
  font-size: 1.2rem;
  font-weight: 500 !important;
}

.content::-webkit-scrollbar {
  position: absolute;
  background-color: transparent;
  width: 6px;
  height: 6px;
}

.content::-webkit-scrollbar-thumb {
  background-color: rgb(173, 174, 177);
  border-radius: 1rem;
  margin-right: 5px;
}
