.landingContainer {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

.slidingContainer {
    z-index: 25;
    position: relative;
    top: 0;
    display: flex;
    overflow-x: hidden;
}

.header {
    margin-top: 1rem;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 26;
}

.header .row {
    align-items: center;
}

.logo {
    font-family: 'Akrobat Bold', sans-serif;
    color: white;
    font-size: 1.5rem;
    border: 0.15rem solid white;
    text-align: center;
    width: 8rem;
    flex-grow: 0;
    user-select: none;
}

.logo:hover {
    cursor: pointer;
}

.logo.big {
    transform: scale(2.6) translateY(-50%);
}

.logo.big:hover {
    cursor: unset;
}

.logo .nash {
    color: #1ac0ea;
}

.main {
    padding-top: 4rem;
    background: url("images/main_bg.png") no-repeat;
    background-size: cover;
    min-width: 100%;
    position: absolute;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main .subTitle {
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 1.7rem;
    text-align: center;
}

.main .separator {
    height: 1.5rem;
    width: 4rem;
    border-bottom: 0.15rem solid white;
    margin-bottom: 2rem;
}

.auth {
    background: url("images/auth_bg.png") no-repeat;
    background-size: cover;
    min-width: 100%;
    position: absolute;
    left: 100%;
    /*flex-direction: column;*/
    /*justify-content: center;*/
    /*align-items: center;*/
}

.loading {
    background: url("images/auth_bg.png") no-repeat;
    background-size: cover;
}

.authInnerContainer {
    position: relative;
    overflow: hidden;
}

.login {
    position: absolute;
    left: 0;
    justify-content: center;
    align-items: center;
}

.register {
    position: absolute;
    left: 100%;
    justify-content: center;
    align-items: center;
}

.loginTab {

}

.loginTab .icon {
    margin-top: -0.1rem;
    margin-right: 0.6rem;
}

.content {
    display: block;
    /*flex-direction: column;*/
}

.contentUnitContainer {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
}

.unit {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2rem 1rem;
}

.unitHeader {
    color: #333333;
    font-family: "Akrobat Bold", sans-serif;
    font-size: 3rem;
}

.unitSeparator {
    height: 0.5rem;
    width: 4rem;
    border-bottom: 0.15rem solid #1ac0ea;
    margin-bottom: 2rem;
}

.servicesContainer {
    display: flex;
    justify-content: center;
    width: 100%;
    background-image: url("contents/Services/images/bg.png");
    background-size: cover;
}

.contactsContainer {
    display: flex;
    justify-content: center;
    background-color: #f8f8f8;
    width: 100%;
}

.contactsContainer .unit {
    padding-left: 0;
    padding-right: 0;
}

.footerContainer {
    display: flex;
    justify-content: center;
    width: 100%;
    background-image: url("Footer/images/bg.png");
    background-size: cover;
    padding: 2rem 1rem;
}
