.container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: #42287D;
  color: white;
  min-width: 16rem;
  width: 16rem;
  user-select: none;
  padding: 2rem 0;
}

.tab {
  display: flex;
  align-items: center;
  padding: 0 2rem;
  height: 3.5rem;
  width: 100%;
  flex-shrink: 0;

  .iconContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 3rem;
  }

  //transition: background-color 0.1s ease-in-out;
}

.tab.logo {
  padding: 0 2.5rem;
  height: 4rem;
}

.tab.active {
  background-color: #4C2D90;
}