.container {
  display: flex;
  justify-content: center;
}

.input {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.1rem solid #333333;
  height: 4rem;

  .icon {
    flex: 0;
    min-width: 5rem;
    display: flex;
    justify-content: center;
  }

  .inputElementContainer {
    flex: 1;

    .element {
      border: none;
      font-family: 'Montserrat', sans-serif;
      color: #333333;
      width: 100%;
      outline: none;
      font-size: 1.3rem;
    }

    .element::placeholder {
      color: #333333;
    }

  }
}

.brief {
  display: flex;
  justify-content: center;
  //width: 100%;

  .text {
    font-family: 'Montserrat', sans-serif;
    text-align: center;
  }

  margin-bottom: 1rem;

}

.main {
  display: flex;
  justify-content: center;

  .imageContainer {
    padding: 1rem;
    flex-grow: 0;

    .image {
      object-fit: cover;
      overflow: hidden;
    }
  }

  .inputs {
    min-height: 20rem;

    flex-grow: 1;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .buttonContainer {
      height: 4rem;
    }

    .button {
      font-family: 'Akrobat Bold', sans-serif;
      font-size: 2rem;
    }
  }
}